package com.example.pokerbanking.data

import com.example.pokerbanking.domain.Player
import com.example.pokerbanking.domain.PlayerRepository
import kotlinx.coroutines.flow.Flow
import kotlinx.coroutines.flow.MutableStateFlow

class FakePlayerRepository : PlayerRepository {
    private val players = MutableStateFlow<List<Player>>(emptyList())
    private var nextPlayerId = 1L

    override fun getPlayers(): Flow<List<Player>> = players

    override suspend fun addPlayer(name: String, displayName: String, phone: String?, email: String?): Result<Long> {
        if (players.value.any { it.name.equals(name, ignoreCase = true) }) {
            return Result.failure(Exception("Player with this name already exists."))
        }
        val newPlayer = Player(
            id = nextPlayerId++,
            name = name,
            displayName = displayName,
            phoneNumber = phone,
            email = email
        )
        players.value += newPlayer
        return Result.success(newPlayer.id)
    }

    override suspend fun updatePlayer(player: Player) {
        val currentList = players.value
        val index = currentList.indexOfFirst { it.id == player.id }
        if (index != -1) {
            val newList = currentList.toMutableList()
            newList[index] = player
            players.value = newList
        }
    }

    override suspend fun deletePlayer(player: Player) {
        players.value = players.value.filterNot { it.id == player.id }
    }
}
